<template>
    <div>
        <div class="addContain">
            <el-form :inline="true" :model="pos" :rules="rules" ref="positionLogin">
                <el-form-item prop="name">
                    <el-input placeholder="请输入内容" size="small" style=" margin-right: 10px;"
                              prefix-icon="el-icon-search" @keydown.enter.native="addPosition"
                              v-model="pos.name">
                    </el-input>
                </el-form-item>
                <el-button type="primary" size="small" @click="addPosition" style="margin-top: 3.5px">添加</el-button>
            </el-form>
        </div>
        <div style="">
            <el-table
                    :data="positions"
                    border
                    size="small"
                    stripe
                    @selection-change="handleSelectionChange"
                    style="width: 70%">
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="id"
                        label="编号"
                        width="80">
                </el-table-column>
                <el-table-column
                        prop="name"
                        label="部门名称"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="createdate"
                        label="创建时间">
                </el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button size="mini" @click="showEditPosition(scope.$index, scope.row)">编辑</el-button>
                        <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-button type="danger" size="small" style="margin-top: 10px"
                       :disabled="this.multipleSelection.length==0" @click="mangDelete">批量删除</el-button>
        </div>
        <el-dialog
                title="提示"
                :visible.sync="dialogVisible"
                width="30%">
            <div>
                <div>
                <el-tag type="success">部门名称</el-tag>
                <el-input v-model="updatePos.name" style="width: 60%;margin-left: 10px;" size="small"></el-input>
                </div>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button size="small" @click="dialogVisible = false">取 消</el-button>
                <el-button size="small" type="primary" @click="updatePosName">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "PosMana",
        data() {
            return {
                pos: {
                    name: ''
                },
                updatePos:{
                    name:'',
                },
                rules: {
                    name: [{required: true, message: '请输入部门名称', trigger: 'blur'}]
                },
                positions: [],
                dialogVisible:false,
                multipleSelection: [],  //代表的就是选中的这些选项
            }
        },
        mounted() {
            this.initPosition();
        },
        methods: {
            mangDelete(){
                this.$confirm('此操作将永久删除这【'+this.multipleSelection.length+'】条记录, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let ids = '?';
                    this.multipleSelection.forEach(item=>{
                        ids += 'ids='+item.id + '&';
                    })
                    this.deleteRequest("/system/basic/pos/"+ids).then(resp=>{
                        if (resp){
                            this.initPosition();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            handleSelectionChange(val){
                this.multipleSelection = val;
            },
            addPosition() {
                this.$refs.positionLogin.validate((valid) => {
                    if (valid) {
                        this.postRequest("/system/basic/pos/", this.pos).then(resp => {
                            if (resp) {
                                this.initPosition();
                                this.pos.name = '';
                            }
                        })
                    } else {
                        //this.$message.error("部门名称不能为空");
                        return false;
                    }
                });
            },
            showEditPosition(index, data) {
                this.dialogVisible = true;
                Object.assign(this.updatePos,data); //此处的data就是一整行的json数据
            },
            updatePosName(){
                this.putRequest("/system/basic/pos/",this.updatePos).then(resp=>{
                    if (resp){
                        this.initPosition();
                        this.dialogVisible=false;
                    }
                })
            },
            handleDelete(index, data) {
                this.$confirm('此操作将永久删除' + data.name + '部门, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/system/basic/pos/" + data.id).then(resp => {
                        if (resp) {
                            this.initPosition();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            initPosition() {
                this.getRequest("/system/basic/pos/").then(resp => {
                    if (resp) {
                        this.positions = resp;
                    }
                })
            }
        }
    }
</script>

<style scoped>
    .addContain {
    }
</style>